import { UserService } from './../../services/user.service';
import { FormGroup, FormControl } from '@angular/forms';
import { GoalsService } from './../../services/goals.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-objetivo',
  templateUrl: './objetivo.page.html',
  styleUrls: ['./objetivo.page.scss'],
})
export class ObjetivoPage implements OnInit {
  form: FormGroup
  goals;
  goal = []; 

  imgOne ='assets/images/haltere.png'
  imgTwo ='assets/images/feijoada.png'
  imgThree ='assets/images/perda-de-peso.png'
  constructor(
    private router: Router,
    private goalsSrv: GoalsService,
    private userSrv: UserService
  ) { }

  ngOnInit() {
    this.loadGoals()
    this.initialForm()
  }

  initialForm(){
    this.form = new FormGroup({
      goals: new FormControl(null)
    })
  }

  nextPage(){
    this.form.value.goals = this.goal
    this.userSrv.insertGoalsUser(this.form.value).subscribe(()=>{
      this.router.navigate(['exercicio'])
    })
    
  }

  onBack(){
    this.router.navigate(['resultado'])
  }

  loadGoals(){
    this.goalsSrv.getGoals().subscribe(data=>{
      this.goals = data
      console.log(this.goals)
    })
  }

  toogle(event){
        console.log(event.detail.value)
        // if(event.detail.checked === true){
        //    this.goal.push(event.detail.value) 
        //    console.log(this.goal)
        // }else{
        //  let arrGoal =  this.goal.filter(goal => goal !== event.detail.value )
        //  this.goal = arrGoal 
        //  console.log(this.goal);
        // }
    
  }
}
